.flex {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  flex-direction: row;
}
.cardbig {
  width: 300px;
  margin-bottom: 20px;
}

.cardbig img {
  width: 300px;
  height: 200px;
  background-color: rgb(255, 255, 255);
  margin-bottom: 20px;
}
.cardsm {
  text-align: left;
  margin: 20px;
}
.cardsm img {
  width: 120px;
  height: 120px;
  margin-bottom: 20px;
}

.dhooan h1 {
  font-family: "Montserrat Medium";
  max-width: 40ch;
  text-align: center;
  transform: scale(0.94);
  animation: scale 3s forwards cubic-bezier(0.5, 1, 0.89, 1);
}
@keyframes scale {
  100% {
    transform: scale(1);
  }
}

.dhooan span {
  display: inline-block;
  opacity: 0;
  filter: blur(4px);
}
.desktopOnly {
  display: inline-block;
}

.dhooan span:nth-child(1) {
  animation: fade-in 0.8s 0.1s forwards cubic-bezier(0.11, 0, 0.5, 0);
}

.dhooan span:nth-child(2) {
  animation: fade-in 0.8s 0.2s forwards cubic-bezier(0.11, 0, 0.5, 0);
}

.dhooan span:nth-child(3) {
  animation: fade-in 0.8s 0.3s forwards cubic-bezier(0.11, 0, 0.5, 0);
}

.dhooan span:nth-child(4) {
  animation: fade-in 0.8s 0.4s forwards cubic-bezier(0.11, 0, 0.5, 0);
}

.dhooan span:nth-child(5) {
  animation: fade-in 0.8s 0.5s forwards cubic-bezier(0.11, 0, 0.5, 0);
}

.dhooan span:nth-child(6) {
  animation: fade-in 0.8s 0.6s forwards cubic-bezier(0.11, 0, 0.5, 0);
}

.dhooan span:nth-child(7) {
  animation: fade-in 0.8s 0.7s forwards cubic-bezier(0.11, 0, 0.5, 0);
}

.dhooan span:nth-child(8) {
  animation: fade-in 0.8s 0.8s forwards cubic-bezier(0.11, 0, 0.5, 0);
}

.dhooan span:nth-child(9) {
  animation: fade-in 0.8s 0.9s forwards cubic-bezier(0.11, 0, 0.5, 0);
}

.dhooan span:nth-child(10) {
  animation: fade-in 0.8s 1s forwards cubic-bezier(0.11, 0, 0.5, 0);
}

.dhooan span:nth-child(11) {
  animation: fade-in 0.8s 1.1s forwards cubic-bezier(0.11, 0, 0.5, 0);
}

.dhooan span:nth-child(12) {
  animation: fade-in 0.8s 1.2s forwards cubic-bezier(0.11, 0, 0.5, 0);
}

.dhooan span:nth-child(13) {
  animation: fade-in 0.8s 1.3s forwards cubic-bezier(0.11, 0, 0.5, 0);
}

.dhooan span:nth-child(14) {
  animation: fade-in 0.8s 1.4s forwards cubic-bezier(0.11, 0, 0.5, 0);
}

.dhooan span:nth-child(15) {
  animation: fade-in 0.8s 1.5s forwards cubic-bezier(0.11, 0, 0.5, 0);
}

.dhooan span:nth-child(16) {
  animation: fade-in 0.8s 1.6s forwards cubic-bezier(0.11, 0, 0.5, 0);
}

.dhooan span:nth-child(17) {
  animation: fade-in 0.8s 1.7s forwards cubic-bezier(0.11, 0, 0.5, 0);
}

.dhooan span:nth-child(18) {
  animation: fade-in 0.8s 1.8s forwards cubic-bezier(0.11, 0, 0.5, 0);
}

@keyframes fade-in {
  100% {
    opacity: 1;
    filter: blur(0);
  }
}

.fixed-bg {
  background-image: url("https://studywebdevelopment.com/images/web-development-101-facebook.jpg");
  min-height: 200px;
  min-width: 100px;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.companyLogo {
  height: 150px;
  margin: 10px;
}
.myform {
  width: 45%;
  background-image: url("https://thumbs.dreamstime.com/b/contact-us-customer-support-hotline-people-connect-call-customer-support-contact-us-customer-support-hotline-people-connect-call-129786983.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  color: white;
  padding: 20px;
}
.SocialMedia {
  display: "flex";
  justify-content: center;
}
.errorMsg {
  color: #ff0000;
  padding: 10px;
}
.mediaIcon {
  width: 90px;
  margin: 30px;
}
.messagesContainer {
  box-shadow: 0 0 20px 8px #d0d0d0;
  padding: 10px;
  border-radius: 50px;
  margin: 20px;
}
.thanks {
  width: 100vw;
}
@media screen and (max-width: 600px) {
  .desktopOnly {
    /* visibility: hidden; */
    font-size: 10px;
  }
  .myform {
    width: 100%;
  }
  .mediaIcon {
    width: 40px;
    margin: 10px;
  }
  .companyLogo {
    height: 70px;
    margin: 10px;
  }
  .messagesContainer {
    box-shadow: 0 0 10px 4px #d0d0d0;
    padding: 5px;
    border-radius: 50px;
    margin: 8px;
    font-size: 10px;
  }
  .cardsm {
    text-align: left;
    margin: 4px;
  }
  .bbblack {
    padding: 0;
  }
  .dhooan h3 {
    font-size: 15px;
  }
}
